import { gsap } from "gsap";
import imgTrailEffect from "./imgTrailEffect";
import horizontalLoop from "./horizontalLoop";
import { Observer } from "gsap/Observer";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { SplitText } from "gsap/SplitText";
import { initScrollReset } from './utils/scrollReset';


gsap.registerPlugin(Observer, ScrollTrigger, SplitText);

let homeScrollTriggers = [];
let tickerCleanups = []; // Store ticker cleanup functions for resize handling
let tickerResizeHandler = null; // Store resize handler reference

// Track the last real mouse position globally
window._lastRealMousePos = { x: window.innerWidth / 2, y: window.innerHeight / 2 };
window.addEventListener('mousemove', function(e) {
    window._lastRealMousePos = { x: e.pageX, y: e.pageY };
});

// Entry point for home page JS
export function initHomePage() {
    initScrollReset();
    
    // Set initial state of hero image BEFORE scrolling to 0
    gsap.set('.hero-img', { 
        opacity: 0, 
        scale: 1.3,
        filter: 'blur(20px)',
        transformOrigin: 'center center'
    });
    
    // Ensure scroll is at 0 before initializing ScrollTrigger
    window.scrollTo(0, 0);
    
    // Use requestAnimationFrame to ensure scroll has taken effect
    requestAnimationFrame(() => {
        window.scrollTo(0, 0);
        requestAnimationFrame(() => {
            ScrollTrigger.refresh();
        });
    });

    // Slide-in top nav (.global-nav-links) when user starts scrolling on Home
    const globalNavLinks = document.querySelector('.global-nav-links');
    let navShowTrigger = null;
    let isResizing = false;
    
    // Helper to calculate nav hide offset
    const getNavHideY = () => {
        if (!globalNavLinks) return -100;
        const cs = getComputedStyle(globalNavLinks);
        const marginTop = parseFloat(cs.marginTop) || 0;
        return -(globalNavLinks.offsetHeight + marginTop);
    };
    
    if (globalNavLinks) {
        // Set initial state based on scroll position
        const scrolled = window.scrollY > 120;
        if (scrolled) {
            gsap.set(globalNavLinks, { transform: 'translate3d(0, 0, 0)' });
        } else {
            gsap.set(globalNavLinks, { transform: `translate3d(0, ${getNavHideY()}px, 0)` });
        }

        navShowTrigger = ScrollTrigger.create({
            trigger: document.body,
            start: '120px top',
            onEnter: () => {
                // Don't animate during resize
                if (isResizing) return;
                gsap.to(globalNavLinks, {
                    transform: 'translate3d(0, 0, 0)',
                    duration: 1,
                    ease: 'power4.out',
                    overwrite: true
                });
            },
            onLeaveBack: () => {
                // Don't animate during resize
                if (isResizing) return;
                gsap.to(globalNavLinks, {
                    transform: `translate3d(0, ${getNavHideY()}px, 0)`,
                    duration: 1,
                    ease: 'power4.out',
                    overwrite: true
                });
            }
        });
        homeScrollTriggers.push(navShowTrigger);
    }

    // Slide-in bottom footer (.global-footer-shell) from below viewport using same timings/trigger
    const globalFooter = document.querySelector('.global-footer-shell');
    let footerShowTrigger = null;
    
    // Helper to calculate footer hide offset
    const getFooterHideY = () => {
        if (!globalFooter) return 100;
        const fcs = getComputedStyle(globalFooter);
        const marginBottom = parseFloat(fcs.marginBottom) || 0;
        return globalFooter.offsetHeight + marginBottom;
    };
    
    if (globalFooter) {
        // Set initial state based on scroll position
        const scrolled = window.scrollY > 120;
        if (scrolled) {
            gsap.set(globalFooter, { transform: 'translate3d(0, 0, 0)' });
        } else {
            gsap.set(globalFooter, { transform: `translate3d(0, ${getFooterHideY()}px, 0)` });
        }

        footerShowTrigger = ScrollTrigger.create({
            trigger: document.body,
            start: '120px top',
            onEnter: () => {
                // Don't animate during resize
                if (isResizing) return;
                gsap.to(globalFooter, {
                    transform: 'translate3d(0, 0, 0)',
                    duration: 1,
                    ease: 'power4.out',
                    overwrite: true
                });
            },
            onLeaveBack: () => {
                // Don't animate during resize
                if (isResizing) return;
                gsap.to(globalFooter, {
                    transform: `translate3d(0, ${getFooterHideY()}px, 0)`,
                    duration: 1,
                    ease: 'power4.out',
                    overwrite: true
                });
            }
        });
        homeScrollTriggers.push(footerShowTrigger);
    }
    
    // Resize handler for nav/footer - update positions and refresh ScrollTrigger
    let navFooterResizeTimeout;
    const navFooterResizeHandler = () => {
        isResizing = true;
        clearTimeout(navFooterResizeTimeout);
        
        navFooterResizeTimeout = setTimeout(() => {
            // After resize settles, update hidden elements to correct position
            requestAnimationFrame(() => {
                const scrolled = window.scrollY > 120;
                if (globalNavLinks && !scrolled) {
                    gsap.set(globalNavLinks, { transform: `translate3d(0, ${getNavHideY()}px, 0)` });
                }
                if (globalFooter && !scrolled) {
                    gsap.set(globalFooter, { transform: `translate3d(0, ${getFooterHideY()}px, 0)` });
                }
                
                // Refresh the specific ScrollTriggers to recalculate positions
                if (navShowTrigger) navShowTrigger.refresh();
                if (footerShowTrigger) footerShowTrigger.refresh();
                
                isResizing = false;
            });
        }, 300);
    };
    window.addEventListener('resize', navFooterResizeHandler);
    
    // Store cleanup reference
    homeScrollTriggers._navFooterResizeHandler = navFooterResizeHandler;

    // Add scroll CTA line animation (only if element exists)
    const ctaLineEl = document.querySelector('.scroll-cta-line');
    const ctaShellEl = document.querySelector('.scroll-cta-shell');
    let scrollCtaTimeline = null;
    if (ctaLineEl) {
        gsap.set(ctaLineEl, {
            scaleY: 0,
            transformOrigin: 'top center'
        });

        scrollCtaTimeline = gsap.timeline({
            repeat: -1,
            defaults: {
                duration: 1,
                ease: "expo.inOut"
            }
        });

        scrollCtaTimeline
            .to(ctaLineEl, {
                scaleY: 1,
                transformOrigin: 'top center'
            })
            .set(ctaLineEl, {
                transformOrigin: 'bottom center'
            })
            .to(ctaLineEl, {
                scaleY: 0
            });
    } else {
        // .scroll-cta-line not found; skipping CTA animation
    }

    // Control scroll CTA visibility based on hero section (fade the whole .scroll-cta-shell when available)
    const heroTickerExists = !!document.querySelector('.hero-ticker-shell');
    const fadeTarget = ctaShellEl || ctaLineEl; // prefer shell, fallback to line
    if (fadeTarget && heroTickerExists) {
        ScrollTrigger.create({
            trigger: '.hero-ticker-shell',
            start: 'top bottom', // Triggers as soon as the section enters viewport
            onEnter: () => {
                if (scrollCtaTimeline) scrollCtaTimeline.pause();
                gsap.to(fadeTarget, {
                    opacity: 0,
                    duration: 0.3
                });
            },
            onLeaveBack: () => {
                if (scrollCtaTimeline) scrollCtaTimeline.play();
                gsap.to(fadeTarget, {
                    opacity: 1,
                    duration: 0.3
                });
            }
        });
    }


    // Create opacity and scale animations only if both elements exist
    // Delay creation until scroll is definitely at 0
    const heroImg = document.querySelector('.hero-img');
    const heroTickerShell = document.querySelector('.hero-ticker-shell');
    if (heroImg && heroTickerShell) {
        // Wait for scroll to settle at 0 before creating ScrollTriggers
        setTimeout(() => {
            window.scrollTo(0, 0);
            requestAnimationFrame(() => {
                // Flag to prevent onUpdate from running until user scrolls
                let allowAnimations = false;
                
                const opacityTimeline = gsap.timeline({
                    scrollTrigger: {
                        trigger: '.img-trail-hero-shell',
                        start: 'top top',
                            endTrigger: '.manifesto-shell',
                            end: 'bottom top',
                        scrub: 1,
                        invalidateOnRefresh: true,
                        onUpdate: (self) => {
                            if (!allowAnimations) return;
                            const progress = self.progress;
                            if (progress <= 0.25) {
                                gsap.to(heroImg, { 
                                    opacity: progress * 4, 
                                    filter: `blur(${20 - (progress * 80)}px)`,
                                    duration: 0.1 
                                });
                            } else if (progress >= 0.35) {
                                gsap.to(heroImg, { opacity: 1 - ((progress - 0.35) * 4), duration: 0.1 });
                            }
                        }
                    }
                });
                homeScrollTriggers.push(opacityTimeline.scrollTrigger);
                
                // Force initial state after ScrollTrigger creation
                gsap.set(heroImg, {
                    opacity: 0,
                    filter: 'blur(20px)',
                    scale: 1.3
                });

                const scaleTween = gsap.to(heroImg, {
                    scale: 1,
                    scrollTrigger: {
                        trigger: '.img-trail-hero-shell',
                        start: 'top top',
                        end: 'bottom top',
                        scrub: true,
                        invalidateOnRefresh: true,
                        enabled: false // Start disabled
                    }
                });
                homeScrollTriggers.push(scaleTween.scrollTrigger);
                
                // Enable animations only when user scrolls
                let hasScrolled = false;
                const enableScrollTriggers = () => {
                    if (!hasScrolled && window.scrollY > 0) {
                        hasScrolled = true;
                        allowAnimations = true;
                        // Trigger immediate update to sync with current scroll position
                        opacityTimeline.scrollTrigger.refresh();
                        scaleTween.scrollTrigger.refresh();
                        window.removeEventListener('scroll', enableScrollTriggers);
                    }
                };
                window.addEventListener('scroll', enableScrollTriggers);
            });
        }, 100);
    }

    // Only run imgTrailEffect when hero is in view
    let imgTrailCleanup = null;
    let activationTimeout = null;

    function enableEffect() {
        clearTimeout(activationTimeout);
        activationTimeout = setTimeout(() => {
            if (typeof imgTrailCleanup === 'function') {
                imgTrailCleanup();
                imgTrailCleanup = null;
            }
            imgTrailCleanup = imgTrailEffect();
        }, 100);
    }

    function disableEffect() {
        clearTimeout(activationTimeout);
        if (typeof imgTrailCleanup === 'function') {
            imgTrailCleanup();
            imgTrailCleanup = null;
        }
    }

    ScrollTrigger.create({
        trigger: '.img-trail-hero-shell',
        start: 'top bottom',
        end: 'bottom 75%',
        //markers: true,
        onEnter: enableEffect,
        onLeave: disableEffect,
        onEnterBack: enableEffect,
        onLeaveBack: disableEffect
    });

        // Store effect cleanup on window so cleanupHomePage can access them if needed
        window._homeImgTrailCleanup = () => {
            try { disableEffect(); } catch (e) { /* ignore */ }
        };

    // Function to initialize all tickers
    function initTickers() {
        // Clean up existing tickers first
        tickerCleanups.forEach(cleanup => {
            try { cleanup(); } catch (e) { /* ignore */ }
        });
        tickerCleanups = [];

        // Loop through each ticker group container on the page with alternating directions
        document.querySelectorAll('.home-project-scroll-txt-shell').forEach((shell, index) => {
            const txts = shell.querySelectorAll('.home-project-txt');
            if (txts.length > 0) {
                // Reset any existing GSAP transforms on the text elements
                gsap.set(txts, { clearProps: 'all' });
                
                // Alternate direction based on index (even = normal, odd = reversed)
                const initialDirection = index % 2 === 0 ? 1 : -1;
                const cleanup = introTicker(txts, shell, initialDirection);
                if (cleanup) tickerCleanups.push(cleanup);
            }
        });
    }

    // Initialize tickers
    initTickers();

    // Debounced resize handler to recreate tickers on breakpoint changes
    let resizeTimeout;
    let lastWidth = window.innerWidth;
    tickerResizeHandler = () => {
        clearTimeout(resizeTimeout);
        resizeTimeout = setTimeout(() => {
            // Only reinitialize if width changed significantly (to catch breakpoint changes)
            const currentWidth = window.innerWidth;
            if (Math.abs(currentWidth - lastWidth) > 50) {
                lastWidth = currentWidth;
                
                // Clear all ScrollTriggers for ticker shells before reinitializing
                ScrollTrigger.getAll().forEach(st => {
                    try {
                        const trigger = st.vars?.trigger;
                        if (trigger && typeof trigger === 'object' && trigger.classList?.contains('home-project-scroll-txt-shell')) {
                            st.kill();
                        }
                    } catch (e) { /* ignore */ }
                });
                
                // Force a reflow to ensure CSS has applied after breakpoint change
                document.querySelectorAll('.home-project-scroll-txt-shell').forEach(shell => {
                    const txts = shell.querySelectorAll('.home-project-txt');
                    txts.forEach(txt => {
                        // Force browser to recalculate styles
                        void txt.offsetWidth;
                    });
                });
                
                // Use double rAF to ensure styles are fully applied before reinitializing
                requestAnimationFrame(() => {
                    requestAnimationFrame(() => {
                        initTickers();
                    });
                });
            }
        }, 300);
    };
    window.addEventListener('resize', tickerResizeHandler);

    // Updated ticker function with proper direction handling
    function introTicker(txtNodes, shell, initialDirection = 1) {
        const baseSpeed = 1.2;
        const maxSpeed = 8;
        const velocityMult = 0.005;

        // Create horizontal loop with proper reversed setting
        const heroLoop = horizontalLoop(txtNodes, {
            repeat: -1,
            speed: baseSpeed,
            reversed: initialDirection < 0 // This is crucial - set reversed based on initial direction
        });

        // Always use positive timeScale regardless of direction
        const absBaseSpeed = Math.abs(baseSpeed);
        heroLoop.timeScale(absBaseSpeed);

        let scrollTimeout;
        let currentDirection = initialDirection;
        let isInView = false;
        
        const pauseLoop = () => {
            isInView = false;
            gsap.killTweensOf(heroLoop);
            heroLoop.pause();
        };
        
        const resumeLoop = () => {
            if (!isInView) {
                isInView = true;
                heroLoop.resume();
                heroLoop.timeScale(absBaseSpeed);
            }
        };

        const st = ScrollTrigger.create({
            trigger: shell,
            start: "top+=20% bottom",
            end: "bottom-=20% top",
            onToggle: ({ isActive }) => {
                if (isActive) {
                    resumeLoop();
                } else {
                    pauseLoop();
                }
            },
            onUpdate: ({ getVelocity }) => {
                // Only process scroll velocity when in view
                if (!isInView) return;
                
                const scrollVelocity = getVelocity();
                // Only consider significant movement
                if (Math.abs(scrollVelocity) > 0.5) {
                    gsap.killTweensOf(heroLoop);
                    
                    const scrollDirection = Math.sign(scrollVelocity);
                    // Flip the scroll direction if this is a reversed ticker
                    const effectiveDirection = initialDirection < 0 ? -scrollDirection : scrollDirection;
                    currentDirection = effectiveDirection;
                    
                    const boostAmount = Math.min(Math.abs(scrollVelocity * velocityMult), maxSpeed);
                    const effectiveSpeed = absBaseSpeed + (Math.abs(effectiveDirection) * boostAmount);
                    
                    heroLoop.timeScale(effectiveSpeed * Math.sign(effectiveDirection));
                }
                
                clearTimeout(scrollTimeout);
                scrollTimeout = setTimeout(() => {
                    // When scrolling stops, normalize speed but keep direction
                    if (isInView) {
                        gsap.to(heroLoop, {
                            timeScale: absBaseSpeed * Math.sign(currentDirection),
                            duration: 0.5,
                            ease: "power1.out",
                            overwrite: true
                        });
                    }
                }, 150);
            }
        });

        // Check if ticker is already in view and start it immediately
        // Use getBoundingClientRect for accurate viewport check
        const checkAndStart = () => {
            const rect = shell.getBoundingClientRect();
            const windowHeight = window.innerHeight;
            // Check if element is in viewport
            const inView = rect.top < windowHeight && rect.bottom > 0;
            if (inView) {
                resumeLoop();
            }
        };
        
        // Run check immediately and after delays to ensure it catches all cases
        checkAndStart();
        requestAnimationFrame(checkAndStart);
        setTimeout(checkAndStart, 50);
        setTimeout(checkAndStart, 150);

        return () => {
            clearTimeout(scrollTimeout);
            gsap.killTweensOf(heroLoop);
            try { st.kill(); } catch (e) { /* ignore */ }
            heroLoop.kill();
        };
    }

    // Animate manifesto text words on scroll using GSAP SplitText
    const manifesto = document.querySelector('.manifesto-txt');
    if (manifesto) {
        // Only split once
        if (!manifesto.classList.contains('split')) {
            const split = new SplitText(manifesto, { type: 'words' });
            manifesto.classList.add('split');
            // Animate words
            gsap.set(split.words, { color: '#2f2f2fff', display: 'inline', whiteSpace: 'normal' });
            gsap.to(split.words, {
                color: '#fff',
                stagger: 0.05,
                duration: 0.5,
                ease: 'power2.out',
                scrollTrigger: {
                    trigger: '.manifesto-shell',
                    start: 'top 80%',
                    end: 'bottom 20%',
                    scrub: true,
                }
            });
        }
    }

    // Add hover animations for featured projects
    document.querySelectorAll('.home-featured-proj-img').forEach(container => {
        const shell = container.querySelector('.home-featured-pj-shell');
        const children = shell.children;
        let isAnimating = false;
        
        container.addEventListener('mouseenter', () => {
            gsap.killTweensOf([shell, children]);
            
            gsap.to(shell, {
                width: '93%',
                height: '93%',
                duration: 0.6,
                ease: 'power4.out'
            });
            
            gsap.to(children, {
                scale: 1.2,
                duration: 0.6,
                ease: 'power4.out'
            });
        });

        container.addEventListener('mouseleave', () => {
            gsap.killTweensOf([shell, children]);
            
            gsap.to(shell, {
                width: '100%',
                height: '100%',
                duration: 0.6,
                ease: 'power4.out'
            });
            
            gsap.to(children, {
                scale: 1,
                duration: 0.4,
                ease: 'power4.out'
            });
        });
    });

    // Add directional hover animations for service items
    let lastMouseY = 0;
    window.addEventListener('mousemove', (e) => { 
        lastMouseY = e.clientY; 
    });

    // Track currently open accordion item
    let currentlyOpenItem = null;

    document.querySelectorAll('.service-item-shell').forEach(item => {
        // Track hover state
        let isHovered = false;
        
        // Click handler for accordion behavior
        item.addEventListener('click', (e) => {
            const serviceSpacer = item.querySelector('.service-link-spacer');
            const serviceParagraph = item.querySelector('.service-paragraph-shell');
            
            if (!serviceSpacer || !serviceParagraph) return;
            
            const isOpen = item.classList.contains('open');
            
            // Close currently open item if it's different from this one
            if (currentlyOpenItem && currentlyOpenItem !== item) {
                const openSpacer = currentlyOpenItem.querySelector('.service-link-spacer');
                const openBg = currentlyOpenItem.querySelector('.service-link-bg');
                const openPlusSymbol = currentlyOpenItem.querySelector('.plus-symbol-svg');
                const openServiceName = currentlyOpenItem.querySelector('.service-name-txt');
                const openParagraph = currentlyOpenItem.querySelector('.service-paragraph-shell');
                
                if (openSpacer) {
                    gsap.killTweensOf(openSpacer);
                    gsap.to(openSpacer, {
                        height: 0,
                        duration: 0.6,
                        ease: "expo.inOut",
                        onUpdate: () => {
                            // Update ScrollSmoother during animation
                            if (window.smootherInstance && typeof window.smootherInstance.refresh === 'function') {
                                window.smootherInstance.refresh();
                            }
                        },
                        onComplete: () => {
                            // Final refresh after animation completes
                            if (window.smootherInstance && typeof window.smootherInstance.refresh === 'function') {
                                window.smootherInstance.refresh();
                            }
                        }
                    });
                }
                
                // Trigger hover out animations on previously open item
                if (openBg) {
                    gsap.killTweensOf(openBg);
                    gsap.to(openBg, {
                        scaleY: 0,
                        duration: 0.6,
                        ease: "expo.out"
                    });
                }
                if (openPlusSymbol) {
                    gsap.killTweensOf(openPlusSymbol);
                    gsap.to(openPlusSymbol, {
                        color: '#FFFFFF',
                        x: 0,
                        rotation: 0,
                        opacity: 1,
                        duration: 0.4,
                        ease: "power2.out"
                    });
                }
                
                // Scale minus symbol back to 0 and hide when closing previous item
                const openMinusSymbol = currentlyOpenItem.querySelector('.minus-symbol');
                if (openMinusSymbol) {
                    gsap.killTweensOf(openMinusSymbol);
                    gsap.to(openMinusSymbol, {
                        scaleX: 0,
                        duration: 0.4,
                        ease: "power2.out",
                        onComplete: () => {
                            gsap.set(openMinusSymbol, { opacity: 0 });
                        }
                    });
                }
                
                if (openServiceName) {
                    gsap.killTweensOf(openServiceName);
                    gsap.to(openServiceName, {
                        color: '#FFFFFF',
                        x: 0,
                        duration: 0.4,
                        ease: "power2.out"
                    });
                }
                
                // Animate paragraph opacity to 0
                if (openParagraph) {
                    gsap.killTweensOf(openParagraph);
                    gsap.to(openParagraph, {
                        opacity: 0,
                        duration: 0.4,
                        ease: "power2.out"
                    });
                }
                
                currentlyOpenItem.classList.remove('open');
            }
            
            if (isOpen) {
                // Close this item
                gsap.killTweensOf(serviceSpacer);
                gsap.to(serviceSpacer, {
                    height: 0,
                    duration: 0.6,
                    ease: "expo.inOut",
                    onUpdate: () => {
                        // Update ScrollSmoother during animation
                        if (window.smootherInstance && typeof window.smootherInstance.refresh === 'function') {
                            window.smootherInstance.refresh();
                        }
                    },
                    onComplete: () => {
                        // Final refresh after animation completes
                        if (window.smootherInstance && typeof window.smootherInstance.refresh === 'function') {
                            window.smootherInstance.refresh();
                        }
                    }
                });
                item.classList.remove('open');
                currentlyOpenItem = null;
                
                // Trigger hover out animations
                const serviceBg = item.querySelector('.service-link-bg');
                const plusSymbol = item.querySelector('.plus-symbol-svg');
                const serviceName = item.querySelector('.service-name-txt');
                
                if (serviceBg) {
                    gsap.killTweensOf(serviceBg);
                    gsap.to(serviceBg, {
                        scaleY: 0,
                        duration: 0.6,
                        ease: "expo.out",
                        onComplete: () => {
                            // If still hovered after closing, re-trigger hover animations
                            if (isHovered) {
                                const rect = item.getBoundingClientRect();
                                const elementCenter = rect.top + (rect.height / 2);
                                const fromTop = lastMouseY < elementCenter;
                                
                                gsap.killTweensOf(serviceBg);
                                gsap.set(serviceBg, {
                                    transformOrigin: fromTop ? 'top center' : 'bottom center',
                                    scaleY: 0
                                });
                                gsap.to(serviceBg, {
                                    scaleY: 1,
                                    duration: 0.6,
                                    ease: "expo.out"
                                });
                            }
                        }
                    });
                }
                if (plusSymbol) {
                    gsap.killTweensOf(plusSymbol);
                    gsap.to(plusSymbol, {
                        color: '#FFFFFF',
                        x: 0,
                        rotation: 0,
                        opacity: 1,
                        duration: 0.4,
                        ease: "power2.out",
                        onComplete: () => {
                            // If still hovered after closing, re-trigger hover animations
                            if (isHovered) {
                                gsap.to(plusSymbol, {
                                    color: '#000000',
                                    x: -25,
                                    rotation: -360,
                                    duration: 0.4,
                                    ease: "power2.out"
                                });
                            }
                        }
                    });
                }
                
                // Scale minus symbol back to 0 and hide when closing
                const minusSymbol = item.querySelector('.minus-symbol');
                if (minusSymbol) {
                    gsap.killTweensOf(minusSymbol);
                    gsap.to(minusSymbol, {
                        scaleX: 0,
                        duration: 0.4,
                        ease: "power2.out",
                        onComplete: () => {
                            gsap.set(minusSymbol, { opacity: 0 });
                        }
                    });
                }
                if (serviceName) {
                    gsap.killTweensOf(serviceName);
                    gsap.to(serviceName, {
                        color: '#FFFFFF',
                        x: 0,
                        duration: 0.4,
                        ease: "power2.out",
                        onComplete: () => {
                            // If still hovered after closing, re-trigger hover animations
                            if (isHovered) {
                                gsap.to(serviceName, {
                                    color: '#000000',
                                    x: 25,
                                    duration: 0.4,
                                    ease: "power2.out"
                                });
                            }
                        }
                    });
                }
                
                // Animate paragraph opacity to 0
                gsap.killTweensOf(serviceParagraph);
                gsap.to(serviceParagraph, {
                    opacity: 0,
                    duration: 0.4,
                    ease: "power2.out"
                });
            } else {
                // Open this item
                const targetHeight = serviceParagraph.offsetHeight;
                gsap.killTweensOf(serviceSpacer);
                gsap.to(serviceSpacer, {
                    height: targetHeight,
                    duration: 0.6,
                    ease: "expo.inOut",
                    onUpdate: () => {
                        // Update ScrollSmoother during animation
                        if (window.smootherInstance && typeof window.smootherInstance.refresh === 'function') {
                            window.smootherInstance.refresh();
                        }
                    },
                    onComplete: () => {
                        // Final refresh after animation completes
                        if (window.smootherInstance && typeof window.smootherInstance.refresh === 'function') {
                            window.smootherInstance.refresh();
                        }
                    }
                });
                item.classList.add('open');
                currentlyOpenItem = item;
                
                // Fade out plus symbol and scale in minus symbol when opening
                const plusSymbol = item.querySelector('.plus-symbol-svg');
                const minusSymbol = item.querySelector('.minus-symbol');
                
                if (plusSymbol) {
                    gsap.killTweensOf(plusSymbol);
                    gsap.to(plusSymbol, {
                        opacity: 0,
                        duration: 0.2,
                        ease: "power2.out"
                    });
                }
                
                if (minusSymbol) {
                    gsap.killTweensOf(minusSymbol);
                    gsap.set(minusSymbol, {
                        transformOrigin: 'left center',
                        scaleX: 0,
                        opacity: 1
                    });
                    gsap.to(minusSymbol, {
                        scaleX: 1,
                        duration: 0.4,
                        delay: 0.6,
                        ease: "power2.out"
                    });
                }
                
                // Trigger hover in animations only if not already hovered
                if (!isHovered) {
                    const serviceBg = item.querySelector('.service-link-bg');
                    const plusSymbol = item.querySelector('.plus-symbol-svg');
                    const serviceName = item.querySelector('.service-name-txt');
                    
                    if (serviceBg) {
                        const rect = item.getBoundingClientRect();
                        const elementCenter = rect.top + (rect.height / 2);
                        const fromTop = lastMouseY < elementCenter;
                        
                        gsap.killTweensOf(serviceBg);
                        gsap.set(serviceBg, {
                            transformOrigin: fromTop ? 'top center' : 'bottom center',
                            scaleY: 0
                        });
                        gsap.to(serviceBg, {
                            scaleY: 1,
                            duration: 0.6,
                            ease: "expo.out"
                        });
                    }
                    
                    if (plusSymbol) {
                        gsap.killTweensOf(plusSymbol);
                        gsap.to(plusSymbol, {
                            color: '#000000',
                            x: -25,
                            rotation: -360,
                            duration: 0.4,
                            ease: "power2.out"
                        });
                    }
                    if (serviceName) {
                        gsap.killTweensOf(serviceName);
                        gsap.to(serviceName, {
                            color: '#000000',
                            x: 25,
                            duration: 0.4,
                            ease: "power2.out"
                        });
                    }
                }
                
                // Animate paragraph opacity to 1
                gsap.killTweensOf(serviceParagraph);
                gsap.to(serviceParagraph, {
                    opacity: 1,
                    duration: 0.4,
                    ease: "power2.out",
                    delay: 0.4
                });
            }
        });

        item.addEventListener('mouseenter', (e) => {
            isHovered = true;
            
            // Don't trigger hover animations if item is open
            if (item.classList.contains('open')) return;
            
            const serviceBg = item.querySelector('.service-link-bg');
            const plusSymbol = item.querySelector('.plus-symbol-svg');
            const serviceName = item.querySelector('.service-name-txt');
            
            if (serviceBg) {
                const rect = item.getBoundingClientRect();
                const elementCenter = rect.top + (rect.height / 2);
                const fromTop = lastMouseY < elementCenter;
                
                gsap.killTweensOf(serviceBg);
                gsap.set(serviceBg, {
                    transformOrigin: fromTop ? 'top center' : 'bottom center',
                    scaleY: 0
                });
                gsap.to(serviceBg, {
                    scaleY: 1,
                    duration: 0.6,
                    ease: "expo.out"
                });
            }

            // Animate text and symbol to black
            if (plusSymbol) {
                gsap.to(plusSymbol, {
                    color: '#000000',
                    x: -25,
                    rotation: -360,
                    duration: 0.4,
                    ease: "power2.out"
                });
            }
            if (serviceName) {
                gsap.to(serviceName, {
                    color: '#000000',
                    x: 25,
                    duration: 0.4,
                    ease: "power2.out"
                });
            }
        });

        item.addEventListener('mouseleave', (e) => {
            isHovered = false;
            
            // Don't trigger hover out if item is open
            if (item.classList.contains('open')) return;
            
            const serviceBg = item.querySelector('.service-link-bg');
            const plusSymbol = item.querySelector('.plus-symbol-svg');
            const serviceName = item.querySelector('.service-name-txt');
            
            if (serviceBg) {
                const rect = item.getBoundingClientRect();
                const elementCenter = rect.top + (rect.height / 2);
                const fromTop = e.clientY < elementCenter;
                
                gsap.killTweensOf(serviceBg);
                gsap.set(serviceBg, {
                    transformOrigin: fromTop ? 'top center' : 'bottom center'
                });
                gsap.to(serviceBg, {
                    scaleY: 0,
                    duration: 0.6,
                    ease: "expo.out"
                });
            }

            // Animate text and symbol back to white
            if (plusSymbol) {
                gsap.to(plusSymbol, {
                    color: '#FFFFFF',
                    x: 0,
                    rotation: 0,
                    duration: 0.4,
                    ease: "power2.out"
                });
            }
            if (serviceName) {
                gsap.to(serviceName, {
                    color: '#FFFFFF',
                    x: 0,
                    duration: 0.4,
                    ease: "power2.out"
                });
            }
        });
    });
}

export function cleanupHomePage() {
    // Kill home-specific ScrollTriggers
    if (Array.isArray(homeScrollTriggers)) {
        // Remove nav/footer resize handler if stored
        if (homeScrollTriggers._navFooterResizeHandler) {
            window.removeEventListener('resize', homeScrollTriggers._navFooterResizeHandler);
        }
        homeScrollTriggers.forEach(st => {
            try { if (st && typeof st.kill === 'function') st.kill(); } catch (e) { /* ignore */ }
        });
        homeScrollTriggers = [];
    }

    // Clean up ticker loops
    if (Array.isArray(tickerCleanups)) {
        tickerCleanups.forEach(cleanup => {
            try { cleanup(); } catch (e) { /* ignore */ }
        });
        tickerCleanups = [];
    }

    // Remove ticker resize handler
    if (tickerResizeHandler) {
        window.removeEventListener('resize', tickerResizeHandler);
        tickerResizeHandler = null;
    }

    // Clear any img-trail effect
    try {
        if (typeof window._homeImgTrailCleanup === 'function') {
            window._homeImgTrailCleanup();
            window._homeImgTrailCleanup = null;
        }
    } catch (e) { /* ignore */ }

    // Kill any ScrollTrigger animations attached to elements used here (best-effort)
    try { ScrollTrigger.refresh(); } catch (e) { /* ignore */ }
}

window.initPageTransitions = function() {
  // Your page-specific GSAP intro animation here
    // Home Page transition animation triggered
  // Animate main-shell translateY from 30% to 0% using translate3d
  const mainShell = document.querySelector('.main-shell');
  if (mainShell) {
    gsap.fromTo(
      mainShell,
      { transform: "translate3d(0, 30%, 0)" },
      {
        transform: "translate3d(0, 0, 0)",
        duration: 1,
        ease: "expo.inOut"
      }
    );
  }
};
